import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';


@IonicPage()
@Component({
  selector: 'page-fund-transfer-other-bank-history03',
  templateUrl: 'fund-transfer-other-bank-history03.html',
})
export class FundTransferOtherBankHistory03Page {
  iTransferDetails;
  transferType;

  constructor(public navCtrl: NavController,
              public navParams: NavParams) {

    this.iTransferDetails = JSON.parse(navParams.get("iTransferDetails"));
    this.transferType = navParams.get("transferType");
  }

  ionViewDidLoad() {

  }

}



